@font-face {
  font-family: 'gotham pro bold';
	src:
		local('gotham pro bold'),
		local('gotham pro-bold'),
		local('gotham-pro-bold'),
		local('gothamprobold'),
		url(../../fonts/GothaProBol.woff);
	font-display:swap;
}

@font-face {
  font-family: 'gotham pro medium';
	src:
		local('gotham pro medium'),
		local('gotham pro-medium'),
		local('gotham-pro-medium'),
		local('gothampromedium'),
		url(../../fonts/GothaProMed.woff);
	font-display:swap;
}

@font-face {
  font-family: 'gotham pro regular';
	src:
		local('gotham pro regular'),
		local('gotham pro-regular'),
		local('gotham-pro-regular'),
		local('gothamproregular'),
		url(../../fonts/GothaProReg.woff);
	font-display:swap;
}

:root {
	--color-primary: #fdac2b;
  --color-gradient-start: #fdac2b;
  --color-gradient-stop: #fdac2b;
}

body {
	font-family: 'gotham pro regular', sans-serif;
	font-size: 14px;
	line-height: 1.7em;
	color: #666;
}

.h1, h1,
.h2, h2,
.h3, h3,
.h4, h4,
.h5, h5,
.h6, h6 {
	font-family: 'gotham pro medium', sans-serif;
  line-height: 1.2em;
}

.h1, h1,
.h2, h2 {
	color: #24140d;
}

.h3, h3,
.h4, h4,
.h5, h5 {
	letter-spacing: -1px;
  color: #181b31;
}

.h1, h1 {
	font-family: 'gotham pro bold', sans-serif;
  font-size: 45px;
}

.h2, h2 {
	font-size: 36px;
  line-height: 1.32em;
}

.h3, h3 {
  font-size: 29px;
}

.h4, h4 {
  font-size: 25px;
}

.h5, h5 {
  font-size: 21px;
}

.h6, h6 {
  font-size: 16px;
  color: #fdac2b
}

.mainbar-wrap {
	padding-left: 35px;
	padding-right: 35px;
}

.main-nav {
	
	> li {
		
		> a {
			font-size: 15px;
			line-height: 1.5em;
			font-weight: 700;
			color: #fff;
		}
		&.is-active > a,
		&.current-menu-item > a,
		&:hover > a,
		> a:hover {
			color: #fff;
		}
	}
}

.header-module {

	.btn {
		color: #fff;
		border-color: #fff;

		&:hover {
			background-color: #fff;
			color: #000;
		}
	}
}

.btn-solid {
	background-color: #fdac2b;
  border-color: #fdac2b;
	color: #000;
	
	&:hover {
		background-color: #fff;
		color: #000;
	}
}

.countdown {
	
	.countdown-period {
		font-size: 14px;
	}
}

.main-footer {
	padding-top: 140px;
	color: #a1a3ab;

	a:not(.btn) {
		color: rgba(240,240,240,0.7);

		&:hover {
			color: #fff;
		}
	}
	.social-icon {

		a {
			color: #fff;
		}
	}
	.widget-title {
		color: #ffe8bf;
	}
}

@media (min-width: 992px) {

	.main-footer {

		section {
			padding-left: 7%;
			padding-right: 7%;
		}
	}
}